.large-hero {
	border-bottom: 10px solid $mainBlue;
	position: relative;
	height: 90vh;
  -webkit-clip-path: polygon(30% 0%, 70% 0%, 100% 0, 100% 80%, 50% 100%, 50% 100%, 0 80%, 0 0);
  clip-path: polygon(30% 0%, 70% 0%, 100% 0, 100% 80%, 50% 100%, 50% 100%, 0 80%, 0 0);
  background: linear-gradient(to bottom, #c1226798, #e4287da2), url("../../images/1.jpg") no-repeat center;
  background-size: cover;
  background-attachment: fixed;


	&__image {
		display: block;
	}

	&__text-content {
		position: absolute;
		top: 50%;
		padding-top: 88px;
		transform: translateY(-50%);
		left: 0;
		width: 100%;
		text-align: center;

		@mixin atMedium {
			padding-top: 60px;
		}
	}

	&__title {
		font-weight: 300;
		color: #fff;
		font-size: 2.4rem;
		margin: 0;

		@mixin atSmall {
			font-size: 4.8rem;
		}
	}
	
	&__subtitle {
		font-weight: 300;
		color: #$mainBlue;
		font-size: 1.5rem;
		margin: 0;

		@mixin atSmall {
			font-size: 2.9rem;
		}
	}

	&__description {
		color: #fff;
		font-size: 1.1rem;
		font-weight: 100;
		text-shadow: 2px 2px 0 rgb(0, 0, 0, .1);
		max-width: 30rem;
		margin-left: auto;
		margin-right: auto;

		@mixin atSmall {
			font-size: 1.875rem;
		}
	}
	
}
